






















































.blog{
    padding-top: 20px;
    width: 80%;
    margin: 0 auto;
    .title_wen{
        width: 100%;
        padding: 20px;
        box-sizing: border-box;
        background: white;
        border: #cccccc solid 1px;
        transition: all ease 1s;
        display: flex;
        align-items: center;
        justify-content: space-around;
        .img{
            width: 34%;
            img{
                width: 100%;
                height: 185px;
            }
        }
        p{
            font-size: 16px;
            margin-bottom: 5px;
        }
        .wen{
            width: 62%;
            .wenTitle{
                font-size: 18px;
                margin-bottom: 10px;
            }
            .span span{
                padding: 3px 5px 3px 0px;
                margin-right: 20px;
                font-size: 16px;
                i{
                    color: red;
                }
            }
        }
        
    }
    .title_wen:hover{
        box-shadow: 2px 0 20px #cccccc;
    }
    .Label{
        position: relative;
        margin-top:20px;
        background: white;
        width: 100%;
        border-radius: 4px;
        display: flex;
        align-items: center;
        padding: 20px;
        box-sizing: border-box;
        border: 1px solid #cccccc;
        ul{
            display: flex;
            li{
                margin: 0 5px;
            }
        }
        .test{
            display: block;
            width: 90px;
            height: 30px;
            background: linear-gradient(90deg,#03a9f4,#f441a5,#ffeb3b,#03a9f4);
            background-size: 400%;
            text-align: center;
            line-height: 30px;
            border-radius: 15px;
            color:white;
            box-sizing: border-box;
            position: relative;
            z-index: 1;
        }
        .test:hover{
             animation: myMove 8s linear infinite;
        }
        .test::before{
            content: '';
            position: absolute;
            top:-5px;
            left: -5px;
            bottom: -5px;
            right: -5px;
            z-index: -1;
            background: linear-gradient(90deg,#03a9f4,#f441a5,#ffeb3b,#03a9f4);
            background-size: 400%;
            border-radius: 20px;
            opacity: 0;
            transition: all ease 0.5s;
        }
        .test:hover::before{
            opacity: 1;
            filter: blur(5px);
            animation: myMove 8s linear infinite;
        }
        @keyframes myMove {
            0%{
                background-position: 0%;
            }
            100%{
                background-position: 400%;
            }
        }
    }
} 
